input,
textarea {
    background: #f7f7f7;
    border: none;
}

.categories-partners {
    margin-top: 103px;
    .title {
        font-size: 38px;
        text-align: center;
    }
    .partners {
        .title {
            display: none;
        }
    }
}

.fancybox-content {
    z-index: 99994;
    background: none;
}

.overflow-y-hidden {
    overflow-y: hidden !important;
}

.fancybox-slide--html .fancybox-close-small {
    top: 15px;
    right: 15px;
    padding: 0;
}

.hide {
    opacity: 0;
}

.show-hide {
    opacity: 1;
    animation: showNote 1.5s ease;
}

.form {
    textarea {
        padding: rem(12);
        font-size: rem(14);
        color: $white;
        background: none;
        border: rem(1) solid $grey;
        border-radius: rem(5);
        resize: none;
        @include tr("all");

        &:focus {
            color: $white;
            border: rem(1) solid $white;
        }

        &::placeholder {
            font-size: rem(12);
            color: $grey_light;
        }
    }

    input {
        min-height: rem(30);
        padding: 0 rem(12);
        font-size: rem(14);
        color: $white;
        background: none;
        border: rem(1) solid $grey;
        border-radius: rem(5);
        @include tr("all");

        &:focus {
            color: $white;
            border: rem(1) solid $white;
        }

        &::placeholder {
            font-size: rem(12);
            color: $grey_light;
        }
    }

    &__input,
    &__textarea {
        display: flex;
        width: 100%;
        margin-bottom: rem(20);
        flex-direction: column;

        label {
            margin-bottom: rem(5);
            font-size: rem(15);
            line-height: 1;
            color: $grey_light;
            @include tr("all");

            @media screen and (max-width: 600px) {
                font-size: 13px;
            }

            &.error {
                font-size: rem(8);
                line-height: 1.2;
                color: $red !important;
            }
        }
    }
}

@keyframes showNote {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes show {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes showFromTop {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
