.product-header {
    @include tr('all');
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    width: 100%;
    color: $grey;
    background: linear-gradient(180deg, #000203 0%, rgba(0, 2, 3, 0) 100%);
    background-color: $black;
    padding: rem(30) 0;
    transform: translateY(-200%);

    @media screen and (max-width: 1399px) {
        padding: rem(12) 0 0;
    }

    .l-default {
        position: static;
        overflow: visible;

        @media screen and (max-width: 1399px) {
            padding: 0;
        }
    }

    &__wrapper {
        display: flex;
        align-items: center;

        @media screen and (max-width: 1399px) {
            flex-wrap: wrap;
        }
    }

    &__logo {
        display: flex;
        align-items: center;
        order: 0;
        width: auto;

        @media screen and (max-width: 1399px) {
            order: 0;   
            width: 31px;
            margin-left: 70px;
            
            svg {
                width: 100%;
                min-width: 100%;
            }
        }
        
        @media screen and (max-width: 991px) {
            position: absolute;
            top: 14px;
            left: 50%;
            transform: translateX(-50%);
            width: 23px;
            order: 1;
            justify-content: center;
            margin-left: auto;
            margin-right: auto;
        }

        svg {
            display: block;
            width: auto;
        }

        &-emblem {
            margin-right: rem(9);

            @media screen and (max-width: 991px) {
                margin-right: 0;
            }

            svg {
                height: rem(30);
            }
        }

        &-text {
            svg {
                height: rem(12);
            }
        }

        .logo--text {
            @media screen and (max-width: 991px) {
                display: none;
            }
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        order: 9;
        position: relative;
        padding: rem(12) rem(35);
        margin-left: rem(50);

        @media screen and (max-width: 1399px) {
            order: 2;   
            margin-left: auto;
            margin-right: 70px;
        }

        @media screen and (max-width: 991px) {
            flex: 1 auto;
            font-size: 10px;
            border: none;
            color: #000203;
            border-radius: 0;
            padding: 0;
            margin: 0;
        }

        &:active {
            padding: rem(8) rem(20);
            margin-left: rem(80);
            margin-top: 4px;
            margin-bottom: 4px;
        }

        &:hover {
            cursor: pointer;
        }

        &-text {
            margin: 0 auto;
            font-size: rem(12);
            line-height: 1;

            @media screen and (max-width: 991px) {
                font-size: rem(10);
                white-space: nowrap;
            }
        }

        &-icon {
            display: none;
            font-size: rem(15);
            line-height: 1;

            @media screen and (max-width: 991px) {
                display: block;
            }
        }

        &-arrow {
            display: none;
            font-size: rem(12);
            line-height: 1;

            @media screen and (max-width: 991px) {
                display: block;
            }
        }
    }

    &__burger {
        @include tr("all");
        display: none;
        position: relative;
        z-index: 2;
        min-width: rem(35);
        min-height: rem(35);
        margin-left: auto;

        @media screen and (max-width: 991px) {
            display: flex;
            order: 3;
            margin-right: 30px;
        }

        @media screen and (max-width: 699px) {
            margin-right: 12px;
        }

        span {
            position: absolute;
            height: rem(2);
            background: $grey_dark;

            &:first-child {
                top: rem(14);
                left: 0;
                width: 100%;
            }

            &:last-child {
                top: rem(24);
                right: 0;
                width: 50%;
            }
        }
    }

    &__menu {
        margin-left: auto;

        .menu {
            &__item {
                &-btn {
                    display: none;
                }
            }
        }

        @media screen and (max-width: 1399px) {
            order: 9;
            width: 100%;
            margin-top: 16px;

            .menu {
                &-product {
                    width: 100%;
                    padding: 14px 70px;
                    background: #111219;
                }
    
                &__items {
                    width: 100%;
                }

                &__item {
                    flex: 1 auto;

                    &:first-child {
                        padding-left: 70px;
                    }

                    &-btn {
                        display: none;
                    }
                }
            }
        }

        @media screen and (max-width: 991px) {
            .menu {
                &-product {
                    padding: 9px 30px;
                }

                &__items {
                    padding: 0;
                }

                &__item {
                    font-size: 10px;
                    padding: 0 30px;
                    margin: 0;

                    &:first-child {
                        padding-left: 0;
                    }

                    &-btn {
                        color: #000203;
                        display: flex;
                    }
                }
            }
        }

        @media screen and (max-width: 767px) {
            .menu {
                &-product {
                    padding: 0;
                }

                &__items {
                    flex-wrap: wrap;
                }

                &__item {
                    display: flex;
                    width: 33.3333%;
                    min-height: 50px;
                    text-align: center;
                    align-items: center;
                    white-space: inherit;
                    padding: 12px 10px;

                    &:first-child, &:nth-child(2), &:nth-child(3) {
                        border-bottom: 1px solid $grey;
                    }
                }
            }
        }
    }

    &__back {
        margin-left: 54px;
        margin-right: 15px;
        &-icon {
            width: 25px;
        }

        @media screen and (max-width: 1399px) {
            order: 1;   
            margin-right: 12px;
            margin-left: 52px;
        }

        @media screen and (max-width: 991px) {
            order: 0;
            margin-left: 30px;
        }

        @media screen and (max-width: 699px) {
            width: 16px;
            margin-left: 12px;
            margin-right: 12px;
        }
    }

    &__name {
        font-size: 16px;

        @media screen and (max-width: 1399px) {
            order: 1;   
        }

        @media screen and (max-width: 991px) {
            order: 0;
        }

        @media screen and (max-width: 699px) {
            font-size: 10px;
        }
    }

    a {
        &:hover {
            color: $white;

            path {
                @include tr('all');
                stroke: $white;
            }
        }
    }

    &-show {
        transform: translateY(0);
    }
}
