.s-solutions {
    padding: rem(150) 0 rem(155);
    // background-image: url(/img/solutions/back.png);
    background-position-x: 110%;
    background-position-y: 55%;
    background-repeat: no-repeat;
    background-size: rem(520);
    @media (max-width: 1600px) {
        // background-image: url(/img/solutions/back_tablet.png);
    }

    @media (max-width: 991px) {
        // background-image: url(/img/solutions/back.png);
        background-position: right bottom;
        background-size: 55%;
        padding: rem(55) 0 rem(80);
    }

    @media (max-width: 767px) {
        background-size: rem(390) rem(615);
    }

    @media (max-width: 500px) {
        background-size: rem(260) rem(420);
        padding: rem(55) 0 rem(25);
    }

    .l-default {
        max-width: rem(1520);
    }

    &__title {
        margin-bottom: rem(45);

        @media (max-width: 600px) {
            text-align: center;
            margin-bottom: 25px;
        }
    }

    &__text {
        max-width: 80%;
        margin-bottom: rem(15);
        font-size: rem(13);
        font-weight: 400;
        line-height: 170%;
        color: $grey_light_more_2;
        @media (min-width: 768px) {
            font-size: rem(15);
            margin-bottom: rem(15);
        }
        @media (min-width: 1024px) {
            font-size: rem(17);
            max-width: 75%;
        }
        @media (min-width: 1600px) {
            font-size: rem(19);
        }

        &--last {
            margin-bottom: 0;
            max-width: 70%;
            font-size: rem(13);
            font-weight: 400;
            line-height: 170%;
            color: $grey_light_more_2;
            @media (min-width: 768px) {
                font-size: rem(15);
            }
            @media (min-width: 1024px) {
                font-size: rem(17);
                max-width: 75%;
            }
            @media (min-width: 1600px) {
                font-size: rem(19);
            }
        }

        &--short {
            margin-top: rem(96);
            margin-left: rem(100);
            .s-solutions__text {
                font-size: 15px;
                line-height: rem(18);
                @media (min-width: 768px) {
                    font-size: 17px;
                    line-height: 20px;
                }
                @media (min-width: 1040px) {
                    font-size: 21px;
                    line-height: 25px;
                }
            }

            @media (max-width: 1040px) {
                margin-top: rem(58);
                margin-left: rem(70);
            }

            @media (max-width: 1040px) {
                margin-left: rem(40);
            }
        }
    }
}
