.news {
    margin-bottom: rem(-85);
    padding: rem(59) 0 rem(104);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    @media (min-width: 500px) {
        margin-bottom: rem(-68);
    }
    .l-content{
        @media (min-width: 768px) {
            padding: 0 60px;
        }
        @media (min-width: 1024px) {
            padding: 0;
            max-width: rem(1180);
        }
    }
    &__btn {
        width: 100%;
        margin-top: rem(130);
        text-align: center;

        &-more {
            display: inline-block;
            margin: 0 auto;
            padding: rem(12) rem(33.5);
            font-size: rem(12);
            font-weight: 400;
            line-height: rem(14);
            border: rem(1) solid #ebebef;
            border-radius: rem(7);
        }
    }
}
