.s-banner {
    position: relative;
    display: flex;
    align-items: flex-end;
    min-height: 502px;

    @media (max-width: 1600px) {
        min-height: 500px;
    }

    @media (max-width: 1025px) {
        min-height: 321px;
    }

    @media (max-width: 991px) {
        min-height: 241px;
    }

    @media (max-width: 600px) {
        min-height: 500px;
    }

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 100%;

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top right;

            @media (max-width: 991px) {
                object-position: center;
            }

            @media (max-width: 600px) {
                object-position: 42%;
            }
        }
    }
}
