.breadcrumbs {
    padding: rem(20) var(--layout-h-padding) 0;

    @media (min-width: 768px) {
        padding: rem(40) var(--layout-h-padding) 0;
    }

    &__item {
        display: flex;
        align-items: center;
        vertical-align: middle;

        &:not(:last-child):after {
            margin-right: rem(4);
            margin-left: rem(4);
            font-size: rem(15);
            font-weight: 500;
            color: $grey_light;
            content: "/";
        }

        &:not(:last-child):hover {
            opacity: 0.8;
        }
    }

    &__icon {
        margin-right: rem(10);
        font-size: rem(13);
    }

    &__link {
        cursor: pointer;
        font-size: rem(15);
        line-height: 1;
        color: $grey_light;
        transition: 0.5s;

        &:hover {
            opacity: 0.8;
        }
    }

    &__link-current {
        color: $grey_light;
    }

    &__list {
        display: flex;
        align-items: center;
        padding: 0;
        list-style-type: none;
        flex-wrap: wrap;
    }
}
