.footer {
    position: relative;
    background: #000203;

    .form {
        order: 2;
        width: 100%;
        margin-bottom: rem(24);

        @media screen and (max-width: 1299px) {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            margin-bottom: 0;
        }

        @media screen and (max-width: 699px) {
            flex-direction: column;
        }

        .btn {
            display: flex;
            align-items: center;
            justify-content: center;
            min-height: rem(44);
            min-width: rem(167);
        }

        &__inputs {
            display: flex;
            align-items: center;
            margin-bottom: rem(23);

            @media screen and (max-width: 1299px) {
                width: 100%;
                margin-bottom: 0;
                margin-right: 30px;
            }

            @media screen and (max-width: 699px) {
                margin-right: 0;
                margin-bottom: 8px;
            }

            @media screen and (max-width: 499px) {
                flex-direction: column;
            }
        }

        &__input {
            position: relative;
            width: 100%;
            margin-right: rem(20);

            @media screen and (max-width: 1025px) {
                max-width: 311px;
            }

            @media screen and (max-width: 991px) {
                max-width: 281px;
            }

            @media screen and (max-width: 499px) {
                max-width: 100%;
                margin-right: 0;
            }

            &:after {
                content: "";
                position: absolute;
                left: 10px;
                top: 50%;
                transform: translateY(-50%);
                display: block;
                height: 20px;
                width: 1px;
                background: $red;

                @media screen and (max-width: 499px) {
                    top: 10px;
                    transform: translateY(0);
                }
            }

            input {
                position: relative;
                width: 100%;
                min-height: rem(44);
                padding-left: rem(23);
                color: $grey_light_more_2;
                background: none;
                border: rem(1) solid $grey;
                border-radius: rem(10);

                @media screen and (max-width: 499px) {
                    margin-bottom: 8px;
                }

                &::placeholder {
                    color: $grey_light_more_2;
                }
            }
        }

        &__submit {
            width: auto;

            @media screen and (max-width: 499px) {
                display: flex;
                width: 100%;
                max-width: 100%;

                .btn {
                    width: 100%;
                    max-width: 100%;
                }
            }
        }
    }

    &__checkbox {
        font-size: rem(15);
        font-weight: 300;
        line-height: 120%;
        color: $grey_light_more;

        @media screen and (max-width: 1299px) {
            max-width: 216px;
            font-size: 10px;
        }

        @media screen and (max-width: 699px) {
            max-width: 100%;
            text-align: center;
        }
    }

    &__top {
        padding-bottom: rem(15);
        border-bottom: rem(1) solid $grey_light;
    }

    &__wrapper {
        position: relative;
        padding-bottom: rem(94);

        &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            background: linear-gradient(
                176deg,
                rgba(32, 32, 32, 0.9808298319327731) 30%,
                rgba(40, 46, 66, 0.008841036414565795) 100%
            );
        }
    }

    &__content {
        margin-bottom: rem(122);
        padding-top: rem(50);

        @media screen and (max-width: 1299px) {
            flex-direction: column;
            margin-bottom: 0;
        }

        @media screen and (max-width: 600px) {
            padding-top: 0;
        }

        .l-default {
            @media screen and (max-width: 600px) {
                display: flex;
                flex-direction: column;
            }
        }

        &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: -1;
            background: linear-gradient(
                357.79deg,
                #202020 1.85%,
                rgba(40, 46, 66, 0) 69.85%
            );
        }

        &-top {
            margin-bottom: 45px;

            @media screen and (max-width: 1299px) {
                margin-bottom: 0;
            }

            @media screen and (max-width: 600px) {
                order: 9;
            }

            .footer {
                &__row {
                    @media screen and (max-width: 600px) {
                        flex-direction: column-reverse;
                    }
                }
            }
        }

        &-bottom {
            .footer {
                &__info {
                    @media screen and (max-width: 600px) {
                        order: 1;
                        margin-top: 0 !important;
                    }
                }
            }
        }
    }

    &__mobile {
        &-menu {
            order: 0;
        }
    }

    &__info {
        width: 100%;
        max-width: rem(554);
        margin-right: rem(100);

        @media screen and (max-width: 1699px) {
            max-width: rem(524);
        }

        @media screen and (max-width: 1499px) {
            margin-right: rem(80);
        }

        @media screen and (max-width: 1299px) {
            display: flex;
            align-items: flex-start;
            max-width: 100%;
            margin-right: 0;
            margin-bottom: 40px;
        }

        @media screen and (max-width: 600px) {
            order: 1;
            flex-direction: column;
            margin-top: 30px;
        }

        &-top {
            order: 0;
            width: 100%;
        }

        &-mobile {
            display: flex;
            align-items: flex-end;
            justify-content: space-between;
            width: 100%;

            @media screen and (max-width: 600px) {
                flex-direction: column;
                justify-content: center;

                &--right {
                    margin: 0 auto;
                }
            }
        }
    }

    &__logo {
        margin-bottom: rem(39);
        font-size: rem(30);
        line-height: 1;

        @media screen and (max-width: 1299px) {
            font-size: 26px;
            margin-bottom: rem(37);
        }

        @media screen and (max-width: 600px) {
            display: none;
        }
    }

    &__text {
        order: 1;
        margin-bottom: rem(32);
        font-size: rem(15);
        font-weight: 600;
        line-height: 120%;
        color: $grey_light_more;

        @media screen and (max-width: 1299px) {
            margin-top: 48px;
            margin-bottom: 16px;
        }
    }

    &__address {
        margin-bottom: rem(46);
        font-size: rem(20);
        font-weight: 900;
        line-height: 120%;
        white-space: nowrap;

        @media screen and (max-width: 1299px) {
            font-size: 15px;
            margin-right: 100px;
            margin-bottom: 0;
        }

        @media screen and (max-width: 767px) {
            font-size: 19px;
            text-align: center;
            margin-right: 0;
            margin-bottom: 42px;
            white-space: pre-wrap;
        }

        &-container {
            order: 3;
        }
    }

    &__contact {
        order: 9;
        display: flex;
        align-items: center;

        @media screen and (max-width: 1299px) {
            align-items: flex-end;
            margin-left: auto;
        }

        @media screen and (max-width: 1023px) {
            flex-direction: column-reverse;
        }

        @media screen and (max-width: 600px) {
            align-items: center;
            flex-direction: column;
            width: 100%;
            margin-left: 0;
        }

        .btn {
            max-width: 100%;
            padding: rem(15) rem(33);
            color: $grey_light_more_2;
            white-space: nowrap;
            margin-left: auto;

            @media screen and (max-width: 1023px) {
                font-size: 12px;
                margin-bottom: 8px;
            }

            @media screen and (max-width: 600px) {
                margin-bottom: 0;
                margin-left: 0;
            }
        }
    }

    &__email {
        margin-right: rem(16);
        font-size: rem(40);
        font-weight: 900;
        line-height: 1;
        color: $grey;
        white-space: nowrap;

        @media screen and (max-width: 1299px) {
            font-size: 30px;
        }

        @media screen and (max-width: 600px) {
            font-size: 40px;
            margin-bottom: 27px;
        }
    }

    &__nav {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        width: 100%;

        @media screen and (max-width: 1499px) {
            margin: 0 rem(-15);
        }

        @media screen and (max-width: 1299px) {
            margin: 0 0 rem(-28);
        }

        @media screen and (max-width: 600px) {
            flex-direction: column;
            order: 0;
            margin: 0;
        }

        &-column {
            width: 25%;
            margin-right: 43px;

            @media screen and (max-width: 1499px) {
                margin-right: 15px;
                margin-bottom: 28px;
            }

            @media screen and (max-width: 600px) {
                width: 100%;
                margin: 0;
                border-bottom: 1px solid #656669;
                padding: 17px 0;
            }

            &:last-child {
                margin-right: 0;
            }

            &.open {
                .footer {
                    &__list {
                        padding: 17px 0;
                    }
                }
            }

            &--nobd {
                @media screen and (max-width: 600px) {
                    border-bottom: none;
                    padding: 0;
                }

                .footer {
                    &__nav {
                        &-top {
                            padding: 0;
                        }
                    }
                }
            }

            &.open {
                .footer {
                    &__nav {
                        &-list {
                            display: block;
                            padding-top: 17px;
                        }

                        &-arrow {
                            transform: rotate(-90deg);

                            svg {
                                fill: $red;
                            }
                        }
                    }
                }
            }
        }

        &-title {
            display: block;
            font-size: rem(20);
            font-weight: 600;
            line-height: 120%;
            color: $grey_white;

            @media screen and (max-width: 1299px) {
                font-size: 12px;
                margin-bottom: 25px;
            }

            @media screen and (max-width: 600px) {
                margin-bottom: 0;
            }

            &--item {
                margin-bottom: 67px;
                @media screen and (max-width: 600px) {
                    width: 100%;
                    border-bottom: 1px solid #656669;
                    padding: 17px 30px !important;
                    margin-bottom: 0;
                }

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        &-list {
            margin-top: rem(30);

            @media screen and (max-width: 1299px) {
                margin-top: 0;
            }

            @media screen and (max-width: 600px) {
                @include tr("all");
                display: none;
                margin-bottom: 0;
                animation: show 0.4s ease-in-out;
            }
        }

        &-item {
            display: block;
            margin-bottom: rem(12);
            font-size: rem(15);
            line-height: 22px;
            color: $grey_light;

            @media screen and (max-width: 1299px) {
                font-size: 14px;
            }

            @media screen and (max-width: 600px) {
                font-weight: 300;
                padding: 0 30px;
                margin-bottom: 17px;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }

        &-top {
            order: 1;
            display: flex;
            align-items: center;
            justify-content: space-between;

            @media screen and (max-width: 600px) {
                padding: 0 30px;
            }
        }

        &-arrow {
            display: none;
            align-items: center;
            justify-content: center;
            font-size: 12px;
            line-height: 1;
            transition: all 0.4s ease-in-out;
            transform: rotate(90deg);

            @media screen and (max-width: 600px) {
                display: flex;
            }
        }
    }

    &__bottom {
        display: flex;
        color: $grey;

        @media screen and (max-width: 1299px) {
            justify-content: center;
            flex-wrap: wrap;
            margin-top: rem(60);
        }

        &-items {
            display: flex;
            width: auto;

            @media screen and (max-width: 600px) {
                flex-wrap: wrap;
                margin: 0 auto;
            }
        }

        &-item {
            display: flex;
            align-items: center;
            font-size: rem(15);
            line-height: 120%;

            @media screen and (max-width: 1299px) {
                font-size: 12px;
            }

            &:not(:last-child) {
                &:after {
                    display: block;
                    width: rem(1);
                    height: rem(22);
                    margin: 0 rem(15);
                    background: $grey;
                    content: "";
                }
            }
        }
    }

    &__copyrights {
        margin-left: auto;
        font-size: rem(15);
        line-height: 120%;

        @media screen and (max-width: 1299px) {
            width: 100%;
            text-align: center;
            margin-top: 10px;
        }

        @media screen and (max-width: 600px) {
            text-align: left;
        }
    }

    &__row {
        display: flex;

        @media screen and (max-width: 1299px) {
            flex-direction: column;
        }
    }
}
