.s-feedback {
    background: rgba(51, 51, 51, 0.1);

    &__title {
        margin-bottom: rem(45);
        font-size: rem(82);
        font-weight: 400;
        line-height: rem(96);
        color: #ebebeb1a;
        letter-spacing: 0.78em;
    }

    &__content {
        display: flex;
        justify-content: center;
        flex-direction: column;
        gap: rem(12);

        @media (min-width: 1024px) {
            gap: 0;
        }

        @media (min-width: 1600px) {
            align-items: center;
            flex-direction: row;
            flex-wrap: nowrap;
        }

        &-top {
            padding: rem(30) 0;

            .l-default {
                @media (min-width: 768px) {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }

                @media (min-width: 1024px) {
                    justify-content: flex-start;
                }
            }

            @media (min-width: 1024px) {
                padding: rem(70) 0;
            }

            @media (min-width: 1600px) {
                padding: rem(80) 0;
                background-color: transparent;
            }
        }

        &-bottom {
            @media (max-width: 1023px) {
                .l-default {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }

            @media (min-width: 1600px) {
                padding: rem(80) 0;
            }
        }
    }

    &__item {
        &-text {
            margin-bottom: rem(20);
            font-size: rem(15);
            font-weight: 400;
            line-height: rem(45);
            text-align: center;
            color: $grey_light_more_2;
            white-space: nowrap;

            @media (min-width: 768px) {
                margin: 0 rem(30) 0 0;
                font-size: rem(19);
                line-height: rem(22);
            }

            @media (min-width: 1024px) {
                margin: 0 rem(96) 0 0;
                font-size: rem(25);
            }

            @media (min-width: 1600px) {
                margin: 0 rem(60) 0 0;
                font-size: rem(38);
                line-height: rem(47);
            }
        }

        &-btn {
            display: flex;
            align-items: center;
            justify-content: center;
            max-width: max-content;
            margin: 0 auto rem(30);
            padding: rem(15) rem(33);
            font-size: rem(12);
            font-weight: 400;
            line-height: 1.2;
            color: $grey_light_more_2;
            border: rem(1) solid $grey_white;
            border-radius: rem(10);
            white-space: nowrap;

            @media (min-width: 768px) {
                margin: 0;
            }

            &:hover {
                border: rem(1) solid $red;
            }
        }

        &-note {
            display: flex;
            align-items: center;
            max-width: rem(550);
            font-size: rem(10);
            font-weight: 300;
            line-height: 1.2;
            color: $grey;

            @media (min-width: 1024px) {
                max-width: rem(730);
            }

            @media (min-width: 1600px) {
                max-width: rem(540);
            }
        }
    }
}
